.button {
    padding: 15px 30px;
    font-size: 18px;
    font-weight: bold;
    text-transform: uppercase;
    border: none;
    border-radius: 10px;
    background-color: tomato;
    color: #fff;
    transition: background-color ease-in-out 350ms;
    cursor: pointer;

    &:hover {
        background-color: #d83619
    }
}